import React, { useState } from 'react'
import classNames from 'classnames'
import { Modal } from 'react-bootstrap'
import { useTranslation } from 'react-i18next'
import modalClose_icon from '../../images/freshBank/nft/modalClose_icon.svg'
import { globalUtils } from '../../utils/globalUtils'
import { NFTController } from './NFTController'

const NftForgeModal = props => {
  const styles = props.styles
  const { t } = useTranslation();

  const burnHandle = () => {
    props.handleClose(props.forgeStatus);
  }

  const showForgingPanel = <div>
    <img src='/images/forging.gif' />
  </div>;

  const showForgingDone = <div>
    <img src='/images/done.svg' />
    <div className={styles.modalHeader2}>{t("forgingSucceed")}</div>
    <div className={styles.modalTips}>{t("forgeingCongratulations", { rarity: NFTController.nftForged })}</div>
  </div>;

  const showForgingError = <div>
    <img src='/images/error.svg' />
    <div className={styles.modalHeader2}>{t("forgingFailed")}</div>
    <div className={styles.modalTips}>{t("forgingFailedDescription")}</div>
  </div>;

  const showForgingCannot = <div>
    <img src='/images/error.svg' />
    <div className={styles.modalHeader2}>{t("forgingFailed")}</div>
    <div className={styles.modalTips}>{t("forgingFailedInsufficient")}</div>
  </div>;

  const showContent = () => {
    let com = null;

    switch (props.forgeStatus) {
      case globalUtils.ForgeStatus.DONE:
        com = showForgingDone;
        break;

      case globalUtils.ForgeStatus.ERROR:
        com = showForgingError;
        break;

      case globalUtils.ForgeStatus.CANNOT:
        com = showForgingCannot;
        break;

      default:
        com = showForgingPanel;
        break;
    }

    return com;
  };

  return (
    <Modal
      show={props.show}
      onHide={props.handleClose}
      className={classNames(styles.nftModal, styles.NftForgeModal)}
      centered>
      <Modal.Body>
        <div className={classNames(styles.bodyContainer)}>
          <div className={classNames(styles.bodyBg)}>
            <div className={styles.bodyStoke}>
              <div className={styles.hd}>
                <span className={styles.title}>{t("forge")}</span>

                <div className={styles.close} onClick={props.handleClose}>
                  <img src={modalClose_icon} />
                </div>
              </div>

              <div className={styles.bodyContent}>
                {showContent()}
              </div>

              <div className={styles.ft} style={{ marginTop: "3em" }}>
                {props.forgeStatus === globalUtils.ForgeStatus.CANNOT ? (
                  <a href="https://app.mojitoswap.finance/nfts/collections/0x6d6a72741E3A413B98fEbB4D328eE86093eD7CE0" target="_blank" className={styles.buy}>
                    {t('NFT.BuyOnMojitoSwap')}
                  </a>
                ) : (<button onClick={burnHandle}>
                  {t("ok")}
                </button>)}
              </div>
            </div>
          </div>
        </div>
      </Modal.Body>
    </Modal>
  )
}



export default NftForgeModal