import React, { useContext, useEffect, useState } from 'react'
import classNames from 'classnames'
import { Container, Collapse, Modal } from 'react-bootstrap'
import { useTranslation } from 'react-i18next'
import { WalletAddressContext, NetworkTypeContext, Web3Context, ReadonlyWeb3Context } from '../../context'
import modalClose_icon from '../../images/freshBank/nft/modalClose_icon.svg'
import legendaryIcon from '../../images/freshBank/nft/legendary_icon.svg'
import epicIcon from '../../images/freshBank/nft/epic_icon.svg'
import normalIcon from '../../images/freshBank/nft/normal_icon.svg'
import arrowWhiteIcon from '../../images/freshBank/nft/arrow_white_icon.svg'
import warning_icon from '../../images/freshBank/nft/warning_icon.svg'
import { galleryData, steps } from './nftData'

const howManyToBurn = new Map();

const BurnPanel = props => {
  const key = props.title;
  const styles = props.styles;
  const t = props.t;

  const [panelStyle, setPanelStyle] = useState("");
  const [icon, setIcon] = useState(null);
  const [label, setLabel] = useState("");
  const [value, setValue] = useState(0);

  useEffect(() => {
    switch (key) {
      case "Legendary":
        setPanelStyle(styles.picYellowWrap);
        setIcon(legendaryIcon);
        setLabel(t('NFT.Legendary'));
        break;

      case "Epic":
        setPanelStyle(styles.picPurpleWrap);
        setIcon(epicIcon);
        setLabel(t('NFT.Epic'));
        break;

      default:
        setPanelStyle(styles.picNormalWrap);
        setIcon(normalIcon);
        setLabel(t('NFT.Normal'));
        break;
    }
  }, []);

  useEffect(() => {
    props.getValue(key, value);
  }, [value]);

  const handleAdd = event => {
    const maxValue = props.data[key]?.length;
    const newValue = value + 1;
    if (newValue <= maxValue) {
      setValue(newValue);
    }
  };

  const handleMinus = event => {
    const newValue = value - 1;
    if (newValue >= 0) {
      setValue(newValue);
    }
  };

  return (<div className={styles.burnItem}>
    <div className={classNames(styles.picWrap, panelStyle)}>
      <img src={icon} />
      <span>{label}</span>
    </div>

    <div className={styles.burnCalc}>
      <span
        onClick={handleMinus}
        className={classNames(styles.sub, {
          [styles.disabled]: value <= 0
        })} />

      <input
        type="number"
        min={0}
        max={props.data[key]?.length}
        step={1}
        value={value}
        readOnly />

      <span
        onClick={handleAdd}
        className={classNames(styles.add, {
          [styles.disabled]: !props.data[key] || value >= props.data[key]?.length || (props.round.burned?.length + value) >= 2
        })} />
    </div>
  </div>)
};

const NftBurnModal = (props) => {
  const styles = props.styles
  const { t, i18n } = useTranslation()
  const { connectedAddress } = useContext(WalletAddressContext);
  const [multiplier, setMultiplier] = useState(1);
  const [countNFTtoBurn, setCountNFTtoBurn] = useState(0);

  const burnHandle = () => {
    props.handleBurn(howManyToBurn);
    props.handleClose()
  }

  const getValue = (key, value) => {
    howManyToBurn.set(key, value);

    let mul = steps[props.round.round - 1].boostMultiplier ?? 1;

    let tempCount = 0;
    Array.from(howManyToBurn).map(nft => {
      tempCount += nft[1];

      galleryData.map(item => {
        if (item.tag === nft[0]) {
          mul *= Math.pow(item.rewards, nft[1]);
        }
      });
    });

    setCountNFTtoBurn(tempCount);
    setMultiplier(mul);
  };

  // render UI
  return (
    <Modal
      show={props.show}
      onHide={props.handleClose}
      className={classNames(styles.nftModal, styles.nftBurnModal)}
      centered
    >
      <Modal.Body>
        <div className={classNames(styles.bodyContainer)}>
          <div className={classNames(styles.bodyBg)}>
            <div className={styles.bodyStoke}>
              <div className={styles.hd}>
                <span className={styles.title}>{t('NFT.Round')}&nbsp;{props.round.round}</span>
                <div className={styles.close} onClick={props.handleClose}>
                  <img src={modalClose_icon} />
                </div>
              </div>
              <div className={styles.bodyContent}>
                <div className={styles.boosMultiWrap}>
                  <span className={styles.bmTitle}>{t('NFT.BoostingMultiplier')}</span>
                  <div className={styles.numChange}>
                    <span className={styles.oldNum}>{steps[props.round.round - 1].boostMultiplier ? steps[props.round.round - 1].boostMultiplier.toLocaleString("en-US", { maximumFractionDigits: 2 }) : "1.0"}X</span>

                    <img src={arrowWhiteIcon} />

                    <span className={styles.newNum}>{multiplier.toFixed(2)}X</span>
                  </div>
                </div>
                <div className={styles.burnList}>
                  <BurnPanel
                    round={props.round}
                    title="Legendary"
                    data={props.data}
                    t={t}
                    getValue={getValue}
                    styles={styles} />

                  <BurnPanel
                    round={props.round}
                    title="Epic"
                    data={props.data}
                    t={t}
                    getValue={getValue}
                    styles={styles} />

                  <BurnPanel
                    round={props.round}
                    title="Rare"
                    data={props.data}
                    t={t}
                    getValue={getValue}
                    styles={styles} />
                </div>

                {(countNFTtoBurn + props.round.burned?.length) >= 2 && (<div className={styles.burnTips}>
                  <img src={warning_icon} />
                  <span>{t('NftBurnModal.ErrorTips')}</span>
                </div>)}
              </div>
              <div className={styles.ft}>
                <button
                  onClick={burnHandle}
                  disabled={props.round.burned?.length >= 2 || (props.round.burned?.length + countNFTtoBurn) > 2 || countNFTtoBurn <= 0}>
                  {t('NftBurnModal.Burn')}
                </button>
              </div>
            </div>
          </div>
        </div>
      </Modal.Body>
    </Modal>
  )
}



export default NftBurnModal