import React, { useContext, useEffect, useState } from 'react'
import classNames from 'classnames'
import { Container, Collapse, Spinner } from 'react-bootstrap'
import { Trans, useTranslation } from 'react-i18next'
import { WalletAddressContext, NetworkTypeContext, Web3Context, ReadonlyWeb3Context, WalletModalContext, LanguageContext } from '../../context'
import { TorToast } from "../../components/littleComponents";
import Loading from '../../images/freshBank/loading.svg';
import { roadMapData, faqListData, galleryData, recordsData, StepStatus, NFTLevel } from './nftData'
import galleryPic1 from '../../images/freshBank/nft/galleryPic1.gif'
import galleryPic2 from '../../images/freshBank/nft/galleryPic2.gif'
import galleryPic3 from '../../images/freshBank/nft/galleryPic3.gif'
import legendaryIcon from '../../images/freshBank/nft/legendary_icon.svg'
import epicIcon from '../../images/freshBank/nft/epic_icon.svg'
import normalIcon from '../../images/freshBank/nft/normal_icon.svg'
import arrowIcon from '../../images/freshBank/nft/arrow_icon.svg'
import goLinkIcon from '../../images/freshBank/nft/goLink_icon.svg'
import { AiOutlinePlus } from "@react-icons/all-files/ai/AiOutlinePlus"
import { AiOutlineMinus } from "@react-icons/all-files/ai/AiOutlineMinus"
import NonWhitelistModal from './nonWhitelistModal'
import GalleryIntroModal from './galleryIntroModal'
import NftBurnModal from './NftBurnModal'
import styles from './nft.module.scss'
import { NFTController } from './NFTController'
import torchlight from '../../images/freshBank/nft/torchlight.gif'
import { globalUtils } from '../../utils/globalUtils'
import warn_icon from '../../images/freshBank/nft/warn_icon.svg'
import add_icon from '../../images/freshBank/nft/add_icon.svg'
import Config from '../../utils/config'
import { BorderedContainer } from './BorderedContainer/BorderedContainer'
import NftForgeModal from './NftForgeModal'
import truncateMiddle from 'truncate-middle'
import { InfiniteList } from '../../components/infiniteList/InfiniteList'

const NFT = (props) => {
  const { language } = useContext(LanguageContext)
  const { connectedAddress } = useContext(WalletAddressContext)
  const { networkType } = useContext(NetworkTypeContext)
  const { web3, readonlyWeb3, hecoMainNetWeb3 } = useContext(Web3Context)
  const { connectWalletModalShow, setConnectWalletModalShow } = useContext(WalletModalContext)
  const { t, i18n } = useTranslation()
  const [nowDate, setNowDate] = useState(null)
  const initialTime = '00'
  const [day, setDay] = useState(initialTime)
  const [hour, setHour] = useState(initialTime)
  const [minute, setMinute] = useState(initialTime)
  const [second, setSecond] = useState(initialTime)
  const [expandStatus, setExpandStatus] = useState([]);
  const [isWhitelist, setIsWhitelist] = useState(false); // 
  const [mintFinished, setMintFinished] = useState(false); //  7.15
  const [showMojitoSwap, setShowMojitoSwap] = useState(false); // 7.11
  const [showMintFailedModal, setShowMintFailedModal] = useState(false) // mint failed ()
  const [showGalleryIntroModal, setShowGalleryIntroModal] = useState(false) // gallery 
  const [showNftBurnModal, setShowNftBurnModal] = useState(false) // nft
  const [showNftForgeModal, setShowNftForgeModal] = useState(false);
  const [forging, setForging] = useState(false);
  const [forgeStatus, setForgeStatus] = useState(globalUtils.ForgeStatus.IDLE);
  const [galleryModalData, setGalleryModalData] = useState(0) //gallery0
  const [tokenIDs, setTokenIDs] = useState([]);
  const [balanceOf, setBalanceOf] = useState(0);
  const [countMinted, setCountMinted] = useState(0);
  const [NFTs, setNFTs] = useState(null);
  const [groupedNFTs, setGroupedNFTs] = useState({
    Rare: [],
    Legendary: [],
    Epic: []
  });
  const [mintSuccess, setMintSuccess] = useState(false);
  const [mintFailed, setMintFailed] = useState(false);
  const [countWhiteList, setCountWhiteList] = useState(0);
  const [roundToBoost, setRoundToBoost] = useState(0);
  const [records, setRecords] = useState([]);
  const [forgingLogsToShow, setForgingLogsToShow] = useState([]);

  const endDate = new Date('2022-07-15T00:00:00.000Z').getTime()
  const buyMojitoSwapDate = new Date('2022-07-11T00:00:00.000Z').getTime()

  let countDownTimer = null;

  const fetchForgingLogs = () => {
    NFTController.getForgingLogs(res => {
      if (res) {
        try {
          let forgingLogs = [...res];
          forgingLogs.map(item => {
            item.level = Object.keys(NFTLevel)[item.level - 1];
          });

          setForgingLogsToShow(forgingLogs);
        } catch (error) {
          console.warn(error)
        }
      }
    });
  };

  const init = async () => {
    await NFTController.init(web3, networkType, connectedAddress);

    setIsWhitelist(await isWhitelistUser(connectedAddress));
    setBalanceOf(await NFTController.balanceOf(connectedAddress));
    setTokenIDs(await NFTController.getTokenIDs(connectedAddress));
    setCountMinted((await NFTController.getPastMintEvent()).length);
    await NFTController.getNFTsBurned();
    await NFTController.computeAllBoostMultiplier();
    setRecords(recordsData());
  };

  useEffect(() => {
    fetchForgingLogs();
  }, []);

  useEffect(() => {
    if (!web3 || !networkType || !connectedAddress || connectedAddress === globalUtils.NA || networkType === "unsupported") {
      return;
    }

    init();
  }, [web3, networkType, connectedAddress]);

  useEffect(() => {
    if (balanceOf <= 0) {
      return setNFTs(null);
    }

    const fetchURIs = async () => {
      setNFTs(await NFTController.fetchURIs(balanceOf));
    }

    fetchURIs();
  }, [balanceOf]);

  useEffect(() => {
    if (!NFTs) {
      return;
    }

    const fetchJSON = async () => {
      // await NFTController.fetchTokenJson(NFTs);
      // setGroupedNFTs(NFTController.groupNFTsByLevel());
      const sorted = [];
      for (let item of NFTs) {
        sorted.push(item);
      }

      for (let i = 0; i < sorted.length; i++) {
        const result = await NFTController.fetchSingleTokenJson(sorted[i]);
        try {
          groupedNFTs[result.attributes[0]?.value].push(result);
          setGroupedNFTs({ ...groupedNFTs });
        } catch (error) {
          console.warn(error);
        }
      }
    };

    fetchJSON();
  }, [NFTs]);

  useEffect(() => {
    if (window.location.hash) {
      scrollToAnchor(window.location.hash.slice(1))
    }
  }, [window.location.hash])

  const countByLevel = level => {
    if (groupedNFTs.length === 0) {
      return 0
    }

    const group = groupedNFTs[level];
    if (group) {
      return group.length;
    } else {
      return 0;
    }
  };

  useState(() => {
    const countDownFn = () => {
      const nowDate = new Date().getTime()

      const diffTime = endDate - nowDate

      if (diffTime <= 0) {
        // 
        setDay(initialTime)
        setHour(initialTime)
        setMinute(initialTime)
        setSecond(initialTime)
        setMintFinished(true)
        clearInterval(countDownTimer)
      } else {
        const countDownSeconds = diffTime / 1000
        const day = Math.floor(countDownSeconds / (60 * 60 * 24))
        const hour = Math.floor(countDownSeconds / (60 * 60)) % 24
        const minute = Math.floor(countDownSeconds / 60) % 60
        const second = Math.floor(countDownSeconds) % 60
        setNowDate(nowDate)
        setDay(day.toString().padStart(2, '0'))
        setHour(hour.toString().padStart(2, '0'))
        setMinute(minute.toString().padStart(2, '0'))
        setSecond(second.toString().padStart(2, '0'))
        setMintFinished(false)
      }

      const diffBuyTime = buyMojitoSwapDate - nowDate
      setShowMojitoSwap(diffBuyTime <= 0)
    }

    countDownTimer = setInterval(countDownFn, 1000)
    countDownFn()

    return () => {
      clearInterval(countDownTimer)
    }
  }, [])


  const faqExpandHandle = (index) => {
    const newExpand = [...expandStatus]
    newExpand[index] = newExpand[index] == null ? true : !newExpand[index]
    setExpandStatus(newExpand)
  }


  const isConnectWallet = () => {
    return connectedAddress && connectedAddress.toLowerCase() !== '0x0000000000000000000000000000000000000000'
  }


  // 
  const isWhitelistUser = async () => {
    const count = parseInt(await NFTController.verifyWhitelist(connectedAddress));
    setCountWhiteList(count);
    return count > 0;
  };

  const handleMint = event => {
    NFTController.mint(
      receipt => {
        setMintSuccess(true)
      },
      (error, receipt) => {
        if (error.code === 4001) {
          // console.log('')
        } else {
          setMintFailed(true)
        }
      }
    );
  };

  const handleBurn = mapToBurn => {
    const IDs = [];
    Array.from(mapToBurn).map(item => {
      if (item[1] > 0) {
        groupedNFTs[item[0]].slice(0, item[1]).map(nft => {
          IDs.push(nft.tokenID);
        });
      }
    });

    NFTController.multiBurn(
      IDs,
      receipt => {
        // NFTController.init(web3, networkType, connectedAddress);
        // init();
        window.location.reload();
      },
      (error, receipt) => { }
    );
  };

  const handleAddWallet = async () => {
    const tokenAddress = Config.torchlight.address[networkType];
    const tokenSymbol = Config.torchlight.symbol;
    const tokenDecimals = 0;
    const tokenImage = 'https://www.torches.finance/favicon.png';

    try {
      // wasAdded is a boolean. Like any RPC method, an error may be thrown.
      const wasAdded = await window.ethereum.request({
        method: 'wallet_watchAsset',
        params: {
          type: 'ERC20', // Initially only supports ERC20, but eventually more!
          options: {
            address: tokenAddress, // The address that the token is at.
            symbol: tokenSymbol, // A ticker symbol or shorthand, up to 5 chars.
            decimals: tokenDecimals, // The number of decimals in the token
            image: tokenImage, // A string url of the token logo
          },
        },
      });

      if (wasAdded) {
        console.log('Thanks for your interest!');
      } else {
        console.log('Your loss!');
      }
    } catch (error) {
      console.log(error);
    }
  }


  const scrollToAnchor = (anchorName) => {
    if (anchorName) {
      // 
      let anchorElement = document.getElementById(anchorName);
      // id
      if (anchorElement) { anchorElement.scrollIntoView({ block: 'start', behavior: 'smooth' }); }
    }
  }

  const handleConnectWallet = event => {
    setConnectWalletModalShow(true);
  };

  // render UI 
  const renderMint = () => {
    if (isConnectWallet()) {
      if (!mintFinished) {
        // 
        if (isWhitelist) {
          return <button className={styles.mint} onClick={handleMint}>{t('NFT.FreeMint')}</button> // 
        } else if (balanceOf > 0) {
          return <button className={styles.mint} disabled>{t("NFT.Minted")}</button> // minted
        } else {
          return <button className={styles.mint} onClick={() => setShowMintFailedModal(true)}>{t('NFT.FreeMint')}</button>  // 
        }
      } else {
        // 
        return <a className={styles.mint} href="#gallery">{t('NFT.CheckMyNFT')}</a>
      }
    } else {
      return <button className={styles.mint} onClick={handleConnectWallet}>{t('Header.ConnectWallet')}</button>
    }

  }

  const renderWhiteListDesc = () => {
    if (countWhiteList === 0 && balanceOf > 0 || mintFinished) {
      return (
        <Trans
          i18nKey="youHaveMint"
          className={styles.desc1}
          values={{ howMany: countMinted > 0 ? countMinted : balanceOf }} />
      )
    } else {
      return (
        <Trans
          i18nKey="youCurrentlyHaveWhitelist"
          className={styles.desc1}
          values={{ howMany: countWhiteList }} />
      )
    }
  }

  const renderCountDown = () => {
    return (
      <section className={styles.countDownContainer}>
        <div className={styles.sloganBg}></div>
        <h1 className={styles.slogan}>{t('NFT.Slogan')}</h1>
        {
          !mintFinished ? (
            <div className={classNames(styles.stage, styles.stage1)}>
              <div className={styles.desc}>{t('NFT.SloganDesc1')}</div>
              <div className={styles.dountDown}>
                <dl>
                  <dd>{day}</dd>
                  <dt>D</dt>
                </dl>
                <span className={styles.colon}>:</span>
                <dl>
                  <dd>{hour}</dd>
                  <dt>H</dt>
                </dl>
                <span className={styles.colon}>:</span>
                <dl>
                  <dd>{minute}</dd>
                  <dt>M</dt>
                </dl>
                <span className={styles.colon}>:</span>
                <dl>
                  <dd>{second}</dd>
                  <dt>S</dt>
                </dl>
              </div>
              {renderMint()}
              {renderWhiteListDesc()}
            </div>
          ) : (
            <div className={classNames(styles.stage, styles.stage2)}>
              <div className={styles.desc}>{t('NFT.SloganDesc2')}</div>
              {renderMint()}
              {renderWhiteListDesc()}
            </div>
          )
        }
      </section>
    )
  }


  const renderRoadMapAndAbout = () => {
    const roadMap = () => {
      return (
        <div className={styles.roadMapContainer}>
          <div className={styles.roadMapList}>
            <div className={styles.line}></div>
            {
              roadMapData(t).map((item, index) => (
                <div className={styles.roadMapItem} key={index}>
                  <div className={styles.circle}></div>
                  <div className={styles.subjectWrap}>
                    <div className={styles.subject}>{item.title}</div>
                    <div className={styles.subjectTime}>{item.time}</div>
                  </div>
                </div>
              ))
            }
          </div>
        </div>
      )
    }

    const about = () => {
      return (
        <div className={styles.aboutContainer}>
          <h2 className={styles.titleWrap}>
            <span className={styles.title}>{t('NFT.AboutTorchlight')}</span>
            <span className={styles.titleStroke}>{t('NFT.AboutTorchlight')}</span>
          </h2>
          <div className={styles.aboutIntroContainer}>
            <dl className={styles.introWrap}>
              <dt>{t('NFT.Torchlight')}</dt>
              <dd>
                <p>{t('NFT.TorchlightIntro1')}</p>
                <p>{t('NFT.TorchlightIntro2')}</p>
              </dd>
            </dl>
            <div className={styles.picWrap}>
              <div className={styles.picInner}>
                <img src={torchlight} />
              </div>
            </div>
          </div>
        </div>
      )
    }


    const mintTips = () => {
      return (
        <div className={styles.mintTipsContainer}>
          <div className={styles.mintTipsInner}>
            <div className={styles.mintTips}>
              <img src={warn_icon} />
              <div className={styles.txtWrap}>
                <Trans
                  i18nKey="NFT.YouHaveMintTips1"
                  values={{ amount: countMinted > 0 ? countMinted : balanceOf }}
                  className={styles.paragraph}
                />
                <div className={styles.paragraph}>
                  {t('NFT.YouHaveMintTips2')}
                </div>
              </div>
            </div>
            <button className={styles.torchlightAddWallet} onClick={handleAddWallet}>
              <span>{t('NFT.TorchlightAddWallet')}</span>
              <img src={add_icon} />
            </button>
          </div>
        </div>
      )
    }

    return (
      <section className={styles.roadMapAndAboutContainer}>
        {roadMap()}
        {about()}
        {balanceOf > 0 && mintTips()}
      </section>
    )
  }

  const renderWire = () => {
    return (
      <div className={classNames(styles.wireWrap)}>
        <div className={styles.wire}></div>
        <div className={styles.wire}></div>
        <div className={styles.wire}></div>
        <div className={styles.wire}></div>
      </div>
    )
  }

  const renderGallery = () => {
    return (
      <div id="gallery" className={styles.galleryContainer}>
        <div className={classNames(styles.inner, styles.galleryInner)}>
          {/* {renderWire()} */}
          <h2 className={styles.titleWrap}>
            <span className={styles.title}>{t('NFT.Gallery')}</span>
            <span className={styles.titleStroke}>{t('NFT.Gallery')}</span>
          </h2>
          {
            showMojitoSwap && (
              <a href="https://app.mojitoswap.finance/nfts/collections/0x6d6a72741E3A413B98fEbB4D328eE86093eD7CE0" target="_blank" className={styles.buy}>
                <span>{t('NFT.BuyOnMojitoSwap')}</span>
                <img src={goLinkIcon} />
              </a>
            )
          }
          <div className={classNames(styles.listWrap)}>
            {
              galleryData.map((item, index) => {
                const owned = countByLevel(item.tag);

                return (
                  <div key={index} className={classNames(styles.itemWrapOuter, {
                    [styles.itemWrapOuterPurple]: item.tag === 'Legendary',
                    [styles.itemWrapOuterBlue]: item.tag === 'Epic',
                    [styles.itemWrapOuterGreen]: item.tag === 'Rare',
                    [styles.itemWrapOuterGrey]: isConnectWallet() && networkType !== "unsupported" && owned === 0
                  })}>
                    <div className={classNames(styles.itemWrap)}>
                      <div className={styles.itemWrapInner}>
                        {
                          isConnectWallet() && networkType !== "unsupported" && (
                            owned > 0 ? (<div className={styles.ownedNum}>
                              {t('NFT.ownedX', { ownedNum: owned })}
                            </div>) : (<div className={styles.ownedNum}>
                              {t('NFT.NotOwned')}
                            </div>)
                          )
                        }
                        <div className={classNames(styles.galleryPicWrap)}>
                          {
                            item.tag === 'Legendary' ? (
                              <img className={styles.galleryPic} src={galleryPic1} />
                            ) : item.tag === 'Epic' ? (
                              <img className={styles.galleryPic} src={galleryPic2} />
                            ) : item.tag === 'Rare' ? (
                              <img className={styles.galleryPic} src={galleryPic3} />
                            ) : ''
                          }
                          <div className={styles.notOwned}>{t('NFT.NotOwned')}</div>
                        </div>
                        <div className={styles.itemInfo}>
                          <div className={styles.tagWrap}>
                            <div className={styles.pcs}>{t('NFT.Pcs', { num: item.pcs })}</div>
                            {
                              item.tag === 'Legendary' ? (
                                <div className={classNames(styles.commonTag, styles.legendaryTag)}>
                                  <img src={legendaryIcon} />
                                  <span>{t('NFT.Legendary')}</span>
                                </div>
                              ) : item.tag === 'Epic' ? (
                                <div className={classNames(styles.commonTag, styles.epicTag)}>
                                  <img src={epicIcon} />
                                  <span>{t('NFT.Epic')}</span>
                                </div>
                              ) : item.tag === 'Rare' ? (
                                <div className={classNames(styles.commonTag, styles.normalTag)}>
                                  <img src={normalIcon} />
                                  <span>{t('NFT.Normal')}</span>
                                </div>
                              ) : ''
                            }

                            <div className={styles.topRewards}>
                              {t('NFT.xTopRewards', {
                                rewards: item.rewards
                              })}
                            </div>
                          </div>
                          <div className={styles.line}></div>
                          <div className={styles.intro} onClick={() => {
                            setShowGalleryIntroModal(true)
                            setGalleryModalData(item)
                          }}>
                            <span>{t('NFT.Introduction')}</span>
                            <img src={arrowIcon} />
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                )
              })
            }
          </div>
        </div>
      </div>
    )
  }

  const titleBarContent = <div className={styles.BorderedContainerTitleBarContent}>
    <div className={styles.title}>
      <span>{t("rulesForge")}</span>
      <span style={{ fontSize: "smaller" }}>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
    </div>

    <a href="https://app.mojitoswap.finance/nfts/collections/0x6d6a72741E3A413B98fEbB4D328eE86093eD7CE0" target="_blank" className={styles.buyMore}>
      <span>{t('NFT.BuyOnMojitoSwap')}</span>
      <img src={goLinkIcon} />
    </a>
  </div>

  const handleForge = async event => {
    const forgeFunc = async () => {
      const tempArrayToForge = groupedNFTs["Rare"].splice(0, 2);
      setGroupedNFTs({ ...groupedNFTs });

      const IDs = [];
      tempArrayToForge.map(nft => {
        IDs.push(nft.tokenID);
      });

      await NFTController.forge(
        IDs,
        null,
        (er, re) => {
          if (er.code === 4001) {
            setForging(false);
          } else {
            setShowNftForgeModal(true);
            setForgeStatus(globalUtils.ForgeStatus.ERROR);
          }

          groupedNFTs["Rare"] = tempArrayToForge.concat(groupedNFTs["Rare"]);
          setGroupedNFTs({ ...groupedNFTs });
        },
        hash => {
          NFTController.detectSynthesizerAPI(3000, hash, res => {
            if (res) {
              setForgeStatus(globalUtils.ForgeStatus.DONE);
            } else {
              setForgeStatus(globalUtils.ForgeStatus.ERROR);
            }

            setShowNftForgeModal(true);
          });
        }
      );
    };

    if (groupedNFTs["Rare"]?.length >= 2) {
      setForging(true);

      const approval = await NFTController.isApprovedForAll();
      if (!approval) {
        await NFTController.setApprovalForAll(
          receipt => {
            setTimeout(async () => {
              await forgeFunc();
            }, 3000);
          },
          (error, receipt) => {
            if (error.code === 4001) {
              setForging(false);
            } else {
              setShowNftForgeModal(true);
              setForgeStatus(globalUtils.ForgeStatus.ERROR);
            }
          }
        );
      } else {
        await forgeFunc();
      }
    } else {
      setShowNftForgeModal(true);
      setForgeStatus(globalUtils.ForgeStatus.CANNOT);
    }
  }

  const renderForge = <>
    {renderWire()}
    <div id="gallery" className={styles.forgeContainer}>
      <div className={classNames(styles.inner, styles.galleryInner)}>

        <h2 className={styles.titleWrap}>
          <span className={styles.title}>{t("torchlightForge")}</span>
          <span className={styles.titleStroke}>{t("torchlightForge")}</span>
        </h2>
      </div>

      <div className={styles.forgeLayout}>
        <BorderedContainer
          strokeWidth={1}
          titleBarContent={titleBarContent}>
          <div className={styles.notificationBar}>{t("rulesForgeContent")}</div>

          <div className={styles.split} />

          <div className={styles.contentContainer}>
            <div>
              <div className={styles.subContainer}>
                <BorderedContainer strokeWidth={3}>
                  <div className={styles.recentlyForge}>
                    <div className={styles.title}>{t("forgingRecord")}</div>

                    {forgingLogsToShow.length === 0 && <div style={{
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                      height: "90%"
                    }}>
                      <img src={Loading} alt="loading" style={{ width: "2em" }} />
                    </div>}

                    {forgingLogsToShow.length > 0 && <InfiniteList
                      scroll={forgingLogsToShow.length > 5}
                      length={forgingLogsToShow.length}>
                      <div className={styles.list}>
                        {forgingLogsToShow.map(forgingLog => {
                          if (!forgingLog) {
                            return;
                          }

                          return <div className={styles.listItem} key={forgingLog.hash}>
                            <div className={styles.accountLabel}>{truncateMiddle(forgingLog.operator, 4, 4, "...")}</div>
                            <div className={styles.itemLabel}>{t("forgingLog", { rarity: forgingLog.level })}</div>
                          </div>
                        })}
                      </div>
                    </InfiniteList>}
                  </div>
                </BorderedContainer>
              </div>

              <div className={styles.subContainer}>
                <BorderedContainer strokeWidth={3}>
                  <div className={styles.launch}>
                    <div>
                      {forging ? (<>
                        <img src='/images/forging.gif' />
                        <div>{t("forging")}</div>
                      </>) : (isConnectWallet() && networkType !== "unsupported" ? <>
                        <div>{t("clickForgeTo")}</div>
                        <button
                          className={classNames(styles.btnAction, styles.normal)}
                          onClick={handleForge}>{t("forge")}</button>
                      </> : <button
                        className={styles.mint}
                        onClick={handleConnectWallet}
                        style={{ marginTop: "-1em" }}>{t('Header.ConnectWallet')}</button>)}
                    </div>
                  </div>
                </BorderedContainer>
              </div>
            </div>
          </div >
        </BorderedContainer >
      </div >
    </div >
  </>;

  const renderFAQ = () => {

    return (
      <div className={styles.FaqContainer}>
        <div className={classNames(styles.inner, styles.FaqInner)}>
          {/* {renderWire()} */}
          <h2 className={styles.titleWrap}>
            <span className={styles.title}>{t('NFT.FAQ')}</span>
            <span className={styles.titleStroke}>{t('NFT.FAQ')}</span>
          </h2>
          <div className={styles.faqList}>
            {
              faqListData(t).map((item, i) => {
                return (
                  <dl key={i} className={classNames(styles.faqItem)}>
                    <dt onClick={() => faqExpandHandle(i)}>
                      <span>{item.title}</span>
                      {expandStatus[i] ? <AiOutlineMinus /> : <AiOutlinePlus />}
                    </dt>
                    <Collapse in={expandStatus[i]}>
                      <dd>{item.content}</dd>
                    </Collapse>
                  </dl>
                )
              })
            }
            <a className={styles.viewFaq} href="https://docs.torches.finance/docs/guides/torchlight-nft" target="_blank">{t('NFT.ViewAllFAQs')}</a>
          </div>
        </div>
      </div>
    )
  }


  const renderTopRewards = () => {
    return (
      <div className={styles.topRewardsContainer}>
        <div className={classNames(styles.inner, styles.topRewardsInner)}>
          {/* {renderWire()} */}
          <h2 className={styles.titleWrap}>
            <span className={styles.title}>{t('NFT.TopRewardsBoosting')}</span>
            <span className={styles.titleStroke}>{t('NFT.TopRewardsBoosting')}</span>
          </h2>
          <div className={styles.tipsContainer}>
            <div className={classNames(styles.tipsWrap, styles.joinTopWrap)}>
              <div className={styles.tipsTitle}>{t('NFT.HowJoinTop')}</div>
              <a href="https://medium.com/@TorchesFinance/how-to-get-top-rewards-and-boosting-your-mining-rate-f922ce8c1ed8" target="_blank" className={styles.tutorial}>
                <span>{t('NFT.ViewTutorial')}</span>
                <img src={goLinkIcon} />
              </a>
              <div className={styles.joinTopList}>
                <div className={styles.joinTopItemOuter}>
                  <div className={classNames(styles.joinTopItem, styles.joinTopItemYellow)}>
                    <div className={styles.num}>1</div>
                    <div className={styles.tips}>{t('NFT.HowJoinTop_step1')}</div>
                    <div className={styles.verticalLine1}></div>
                    <div className={styles.verticalLine2}></div>
                  </div>
                  <div className={styles.sNum}>S01</div>
                </div>
                <div className={styles.joinTopItemOuter}>
                  <div className={classNames(styles.joinTopItem, styles.joinTopItemGreen)}>
                    <div className={styles.num}>2</div>
                    <div className={styles.tips}>{t('NFT.HowJoinTop_step2')}</div>
                    <div className={styles.verticalLine1}></div>
                    <div className={styles.verticalLine2}></div>
                  </div>
                  <div className={styles.sNum}>S02</div>
                </div>
                <div className={styles.joinTopItemOuter}>
                  <div className={classNames(styles.joinTopItem, styles.joinTopItemBlue)}>
                    <div className={styles.num}>3</div>
                    <div className={styles.tips}>{t('NFT.HowJoinTop_step3')}</div>
                    <div className={styles.verticalLine1}></div>
                    <div className={styles.verticalLine2}></div>
                  </div>
                  <div className={styles.sNum}>S03</div>
                </div>
              </div>
            </div>
            <div className={classNames(styles.tipsWrap, styles.calMultWrap)}>
              <div className={styles.tipsTitle}>{t('NFT.HowCaluBoosting')}</div>
              <div className={styles.calMultList}>
                <div className={classNames(styles.calMultItemOuter, styles.calMultItemOuterGreen)}>
                  <div className={styles.lowerBg}></div>
                  <div className={classNames(styles.num, styles.numGreen)}>01</div>
                  <div className={styles.calMultItem}>
                    <div className={classNames(styles.line, styles.line1)}></div>
                    <div className={classNames(styles.line, styles.line2)}></div>
                    <div className={classNames(styles.line, styles.shortLine1)}></div>
                    <div className={classNames(styles.line, styles.shortLine2)}></div>
                    <div className={styles.txt}>{t('NFT.HowCaluBoosting_step1')}</div>
                  </div>
                </div>
                <div className={classNames(styles.calMultItemOuter, styles.calMultItemOuterYellow)}>
                  <div className={styles.lowerBg}></div>
                  <div className={classNames(styles.num, styles.numYellow)}>02</div>
                  <div className={styles.calMultItem}>
                    <div className={classNames(styles.line, styles.line1)}></div>
                    <div className={classNames(styles.line, styles.line2)}></div>
                    <div className={classNames(styles.line, styles.shortLine1)}></div>
                    <div className={classNames(styles.line, styles.shortLine2)}></div>
                    <div className={styles.txt}>{t('NFT.HowCaluBoosting_step2')}</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }

  const getActionTitle = step => {
    let title = "";
    switch (step.status) {
      case StepStatus.past:
        if (step.reward.gt(0)) {
          title = t('NFT.Airdrop');
        } else {
          title = t('NFT.End');
        }
        break;

      case StepStatus.now:
        const now = new Date();
        if (step.startBoost < now && now > step.stopBoost) {
          title = t('NFT.OnBoosting');
        } else {
          title = t('NFT.BoostNow');
        }
        break;

      default:
        title = t('NFT.Waiting');
        break;
    }
    return title;
  };

  const handleBoost = event => {
    setRoundToBoost(records[parseInt(event.currentTarget.id)]);
    setShowNftBurnModal(true);
  };

  const showBoostMultiplier = step => {
    if (step.status === StepStatus.waiting) {
      return "-";
    } else {
      return step.boostMultiplier.toLocaleString("en-US", { maximumFractionDigits: 2 }) + "x";
    }
  };


  const renderRecordsRow = () => {
    return records.length > 0 ? records.map((item, index) => {
      return (<tr key={index}>
        <td>
          <div className={styles.mobilTableRound}>{t('NFT.Round')}</div>
          <div>{item.round}</div>
        </td>

        <td>
          <div className={styles.mobilTableTh}>{t('NFT.burningDuration')}</div>
          <div>
            <span>{item.time}</span>
            {(item.status === StepStatus.past || item.stopTime < nowDate) && <span className={styles.end}>{t('NFT.End')}</span>}
          </div>
        </td>

        <td>
          <div className={styles.mobilTableTh}>{t('NFT.boostingDuration')}</div>
          <div>
            <span>{item.boostTime}</span>
          </div>
        </td>

        <td>
          <div className={styles.mobilTableTh}>{t('NFT.BoostingMultiplier')}</div>
          <div>{showBoostMultiplier(item)}</div>
        </td>

        <td>
          <div className={styles.mobilTableTh}>{t('NFT.TopBoostingRewards')}</div>
          <div>{item.reward.toNumber().toLocaleString(language, { maximumFractionDigits: 4, useGrouping: false })}</div>
        </td>

        <td>
          <button
            id={index}
            className={classNames(styles.btnAction, {
              [styles.past]: item.status === StepStatus.past,
              [styles.now]: item.status === StepStatus.now,
              [styles.waiting]: item.status === StepStatus.waiting,
              // [styles.onBoosting]: item.burned?.length >= 2
              [styles.onBoosting]: nowDate > item.startBoost && nowDate < item.stopBoost
            })}
            onClick={handleBoost}>
            {getActionTitle(item)}
          </button>
        </td>
      </tr>)
    }) : (
      <tr>
        <td colSpan="5" className={styles.alignCenter}>
          <Spinner animation="border" variant="success" size="lg" />
        </td>
      </tr>
    )
  }

  const renderRecords = () => {
    return (
      <div className={styles.recordsContainer}>
        <div className={styles.inner}>
          <h2 className={styles.titleWrap}>
            <span className={classNames(styles.title, styles.recordsTitle)}>{t('NFT.Records')}</span>
            <span className={styles.titleStroke}>{t('NFT.Records')}</span>
          </h2>
          <div className={styles.recordsTableOuter}>
            <table className={styles.recordsTable}>
              <thead>
                <tr>
                  <th><span>{t('NFT.Round')}</span></th>
                  <th><span>{t('NFT.burningDuration')}</span></th>
                  <th><span>{t('NFT.boostingDuration')}</span></th>
                  <th><span>{t('NFT.BoostingMultiplier')}</span></th>
                  <th><span>{t('NFT.TopBoostingRewards')}</span></th>
                  <th><span>{t('NFT.Action')}</span></th>
                </tr>
              </thead>

              <tbody>
                {isConnectWallet() && networkType !== "unsupported" ? renderRecordsRow() : (
                  <tr>
                    <td colSpan="5" className={classNames(styles.alignCenter)}>
                      <button className={styles.mint} onClick={handleConnectWallet}>{t('Header.ConnectWallet')}</button>
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        </div>
      </div >
    )
  }

  const handleCloseForgeModal = forgeResult => {
    if (forgeResult === globalUtils.ForgeStatus.DONE) {
      window.location.reload();
    } else {
      setShowNftForgeModal(false);
      setForging(false);
    }
  };

  // render UI
  return (
    <div className={styles.nftContainer}>
      {renderCountDown()}
      <div className={styles.container}>
        {renderRoadMapAndAbout()}
        {renderGallery()}
        {renderForge}

        {/*  */}
        {mintFinished && (
          <div id="topRewards" className={styles.stage2Container}>
            {renderTopRewards()}
            {renderRecords()}
          </div>
        )}
        {renderFAQ()}
      </div>
      {
        showMintFailedModal && (
          <NonWhitelistModal
            styles={styles}
            showMojitoSwap={showMojitoSwap}
            show={showMintFailedModal}
            handleClose={() => setShowMintFailedModal(false)}
          />
        )
      }
      {
        showGalleryIntroModal && (
          <GalleryIntroModal
            styles={styles}
            data={galleryModalData}
            show={showGalleryIntroModal}
            handleClose={() => setShowGalleryIntroModal(false)}
          />
        )
      }

      {showNftBurnModal && (<NftBurnModal
        round={roundToBoost}
        styles={styles}
        show={showNftBurnModal}
        data={groupedNFTs}
        handleBurn={handleBurn}
        handleClose={() => setShowNftBurnModal(false)}
      />)}

      {showNftForgeModal && <NftForgeModal
        styles={styles}
        show={showNftForgeModal}
        forgeStatus={forgeStatus}
        handleClose={handleCloseForgeModal} />}

      <TorToast
        type='success'
        show={mintSuccess}
        showMs={3000}
        onClose={() => {
          setMintSuccess(false);
          init();
        }}
      >
        {t('NFT.mintSuccess')}
      </TorToast>

      <TorToast
        type='error'
        show={mintFailed}
        showMs={3000}
        onClose={() => setMintFailed(false)}
      >
        {t('NFT.mintFailed')}
      </TorToast>
    </div>
  )
}


export default NFT