import React, {useContext, useState} from 'react'
import classNames from 'classnames'
import { Container, Collapse, Modal } from 'react-bootstrap'
import { useTranslation } from 'react-i18next'
import { WalletAddressContext, NetworkTypeContext, Web3Context, ReadonlyWeb3Context } from '../../context'
import Config from '../../utils/config';
import modalClose_icon from '../../images/freshBank/nft/modalClose_icon.svg'
import goLinkWhite_icon from '../../images/freshBank/nft/goLinkWhite_icon.svg'
import legendaryIcon from '../../images/freshBank/nft/legendary_icon.svg'
import epicIcon from '../../images/freshBank/nft/epic_icon.svg'
import normalIcon from '../../images/freshBank/nft/normal_icon.svg'


const GalleryIntroModal = (props) => {
  const styles = props.styles
  const { t, i18n } = useTranslation()
  const { networkType } = useContext(NetworkTypeContext)

  return (
    <Modal
      show={props.show}
      onHide={props.handleClose}
      className={classNames(styles.nftModal, styles.galleryIntroModal)}
      centered
    >
    <Modal.Body>
      <div className={classNames(styles.bodyContainer)}>
        <div className={classNames(styles.bodyBg)}>
          <div className={styles.bodyStoke}>
            <div className={styles.hd}>
              <span className={styles.title}>
                {t('GalleryIntroModal.Torchlight', {
                  tag: props.data.tag === 'Legendary' ? t('NFT.Legendary') : props.data.tag === 'Epic' ? t('NFT.Epic') : props.data.tag === 'Normal' ? t('NFT.Normal') : ''
                })}
              </span>
              <div className={styles.close} onClick={props.handleClose}>
                <img src={modalClose_icon} />
              </div>
            </div>
            <div className={styles.bodyContent}>
             <div className={classNames(styles.infoWrap, styles.addressWrap)}>
               <div className={styles.title}>{t('GalleryIntroModal.ContractAddress')}</div>
               <div className={styles.address}>
                 <span>{Config.torchlight.address[networkType]}</span>
                 {/* <img src={goLinkWhite_icon}/> */}
               </div>
             </div>
             <div className={classNames(styles.infoWrap, styles.featuresWrap)}>
               <div className={styles.title}>{t('GalleryIntroModal.Features')}</div>
               <div className={styles.featureList}>
                 <div className={styles.featureItem}>
                   <div className={styles.itemHd}>{t('GalleryIntroModal.TotalAmount')}</div>
                   <div className={styles.pcs}>{t('NFT.Pcs', {num: props.data.pcs})}</div>
                 </div>
                 <div className={styles.featureItem}>
                   <div className={styles.itemHd}>{t('GalleryIntroModal.Level')}</div>
                   {
                      props.data.tag === 'Legendary' ? (
                        <div className={classNames(styles.commonTag, styles.legendaryTag)}>
                          <img src={legendaryIcon}/>
                          <span>{t('NFT.Legendary')}</span>
                        </div>
                      ) : props.data.tag === 'Epic' ? (
                        <div className={classNames(styles.commonTag, styles.epicTag)}>
                          <img src={epicIcon}/>
                          <span>{t('NFT.Epic')}</span>
                        </div>
                      ) : props.data.tag === 'Rare' ? (
                        <div className={classNames(styles.commonTag, styles.normalTag)}>
                          <img src={normalIcon}/>
                          <span>{t('NFT.Normal')}</span>
                        </div>
                      ) : ''
                    }
                 </div>
                 <div className={styles.featureItem}>
                    <div className={styles.itemHd}>{t('GalleryIntroModal.Benefit')}</div>
                    <div className={styles.topRewards}>
                      {t('NFT.xTopRewards', {
                        rewards: props.data.rewards
                      })}
                    </div>
                 </div>
               </div>
             </div>
            </div>
            <div className={styles.ft}>
              <button onClick={props.handleClose}>
                OK
              </button>
            </div>
          </div>
        </div>
      </div>
    </Modal.Body>
    </Modal>
  )
}



export default GalleryIntroModal