import { BigNumber } from 'bignumber.js'
import { find, findIndex, groupBy, isUndefined, remove, sortBy } from "lodash-es";
import config from "../../utils/config";
import { globalUtils } from "../../utils/globalUtils";
import { steps, StepStatus } from "./nftData";

export const NFTController = {
	_NFTContract: null,
	_web3: null,
	_networkType: config.headerConfigData.network.kcc.chainAsKey,
	_account: null,
	_tokenIDs: null,
	_tokenURIs: null,
	_tokenJSON: null,
	_steps: null,
	_now: new Date(),
	_tryCountForSynthesizer: 0,
	nftForged: null,

	init: async function (web3, networkType, connectedAddress) {
		this._web3 = web3;
		this._networkType = networkType;
		this._account = connectedAddress;
		this._tokenIDs = new Map();
		this._tokenURIs = new Map();
		this._tokenJSON = new Map();

		this._steps = steps;
		this.formatSteps();
		await this._getAirdrop();
	},

	verifyWhitelist: async function (address) {
		return parseInt(await (await this._getNFTContract()).methods.verifyWhitelist(address).call());
	},

	mint: async function (callback, revertCallback) {
		this._runContractMethod(
			(await this._getNFTContract()).methods.mint,
			[],
			callback,
			revertCallback
		);
	},

	multiBurn: async function (arrayToBurn, callback, revertCallback) {
		let func = (await this._getNFTContract()).methods.burn;
		const calls = []
		arrayToBurn.map(item => {
			calls.push(func(item).encodeABI())
		});

		func = (await this._getNFTContract()).methods.multicall;
		this._runContractMethod(
			func,
			[calls],
			callback,
			revertCallback
		);
	},

	getTokenIDs: async function (owner) {
		const all = await this.balanceOf(owner);
		const IDs = [];
		for (let i = 0; i < all; i++) {
			IDs.push(await this.tokenOfOwnerByIndex(owner, i));
		}
		return IDs;
	},

	balanceOf: async function (owner) {
		return parseInt(await (await this._getNFTContract()).methods.balanceOf(owner).call());
	},

	formatSteps: function () {
		this._steps.map(step => {
			step.startTime = new Date(step.startTime);
			step.stopTime = new Date(step.stopTime);
			step.burned = [];
			step.boostMultiplier = 0;

			if (step.stopTime < this._now) {
				step.status = StepStatus.past;
			} else if (step.startTime > this._now) {
				step.status = StepStatus.waiting;
			} else {
				step.status = StepStatus.now;
			}
		});
	},

	getBurnLog: async function (owner) {
		return await (await this._getNFTContract()).methods.getBurnLog(owner).call();
	},

	getNFTsBurned: async function () {
		const logs = await this.getBurnLog(this._account);
		logs.map(log => {
			const logTime = new Date(parseInt(log[0]) * 1000);
			this._steps.map(step => {
				if (step.startTime < logTime && step.stopTime >= logTime) {
					step.burned.push({
						id: parseInt(log[1])
					});
				}
			});
		});
	},

	computeAllBoostMultiplier: async function () {
		for (let i = 0; i < this._steps.length; i++) {
			await this.computeBoostMultiplier(i);
		}
	},

	computeBoostMultiplier: async function (indexOfSteps) {
		const step = this._steps[indexOfSteps];

		if (step.boostMultiplier) {
			return step.boostMultiplier;
		}

		for (let i = 0; i <= indexOfSteps; i++) {
			let mul = 1;
			for (let j = 0; j < step.burned.length; j++) {
				const item = step.burned[j];
				if (!item.multiplier) {
					try {
						const result = await (await fetch(config.torchlight.baseURI[this._networkType] + item.id)).json();
						if (result) {
							item.multiplier = result.attributes[findIndex(result.attributes, { "trait_type": "multiplier" })]?.value ?? 0;
						} else {
							item.multiplier = 0;
						}
					} catch (error) {
						console.error(error);
						item.multiplier = 0;
					}
				}

				mul *= item.multiplier;
			}

			step.boostMultiplier = step.initMultiplier * mul;

			if (i > 0) {
				step.boostMultiplier *= (this._steps[indexOfSteps - 1].boostMultiplier - 1) / 2 + 1;
			}

		}
	},

	fetchURIs: async function (balance) {
		for (let i = 0; i < balance; i++) {
			const id = await this.tokenOfOwnerByIndex(this._account, i);
			if (id >= 0) {
				await this.tokenURI(id);
			}
		}

		return new Map(this._tokenURIs);
	},

	fetchSingleTokenJson: async function (token) {
		try {
			const result = await (await fetch(token[1])).json();
			if (result) {
				result.tokenID = token[0];
				return result;
			}
		} catch (error) {
			console.warn(error);
		}
	},

	fetchTokenJson: async function (tokens) {
		const sorted = [];
		for (let item of tokens) {
			sorted.push(item);
		}

		for (let i = 0; i < sorted.length; i++) {
			const item = sorted[i];

			if (!this._tokenJSON.has[item[0]]) {
				try {
					const result = await (await fetch(item[1])).json();
					if (result) {
						result.tokenID = item[0];
						this._tokenJSON.set(item[0], result);
					}
				} catch (error) {
					console.warn(error);
				}
			}
		}

		return this._tokenJSON;
	},

	groupNFTsByLevel: function () {
		const NFTs = Array.from(this._tokenJSON, item => item[1]);
		return groupBy(NFTs, nft => nft.attributes[0]?.value);
	},

	tokenOfOwnerByIndex: async function (owner, indexOfNFT) {
		const key = String(indexOfNFT);

		if (this._tokenIDs.has(key)) {
			return this._tokenIDs.get(key);
		}

		try {
			const tokenID = await (await this._getNFTContract()).methods.tokenOfOwnerByIndex(owner, indexOfNFT).call();
			this._tokenIDs.set(key, tokenID);
			return tokenID;
		} catch (error) {
			console.warn(error);
			return -1;
		}
	},

	tokenURI: async function (id) {
		const key = String(id);

		if (this._tokenURIs.has(key)) {
			return this._tokenURIs.get(key);
		}

		try {
			const uri = await (await this._getNFTContract()).methods.tokenURI(id).call();
			this._tokenURIs.set(key, uri);
			return uri;
		} catch (error) {
			console.warn(error);
		}
	},

	getPastMintEvent: async function () {
		try {
			return (await this._getNFTContract()).getPastEvents("allEvents", {
				fromBlock: config.torchlight.deployAt[this._networkType],
				toBlock: globalUtils.LiteralBlockNumber.LATEST,
				topics: [
					config.torchlight.topics.transfer,
					globalUtils.ZERO_32,
					this._account.replace(globalUtils.HEXSTRING_PREFIX, globalUtils.ADDRESS32_PREFIX)
				]
			});
		} catch (error) {
			console.error(error);
			return [];
		}
	},

	isApprovedForAll: async function () {
		return (await this._getNFTContract()).methods.isApprovedForAll(this._account, config.torchlight.forge[this._networkType].address).call();
	},

	setApprovalForAll: async function (callback, revertCallback) {
		this._runContractMethod(
			(await this._getNFTContract()).methods.setApprovalForAll,
			[config.torchlight.forge[this._networkType].address, true],
			callback,
			revertCallback
		);
	},

	forge: async function (args, callback, revertCallback, hashCallback) {
		const abi = await globalUtils.loadABI(config.torchlight.forge[this._networkType].ABI);
		const contract = new this._web3.eth.Contract(abi, config.torchlight.forge[this._networkType].address);
		this._runContractMethod(
			contract.methods.synthesize,
			[...args],
			callback,
			revertCallback,
			null,
			hashCallback
		);
	},

	detectSynthesizerAPI: function (delay, hash, callback) {
		setTimeout(() => {
			window.requestIdleCallback(async () => {
				try {
					const res = await (await fetch(config.torchlight.synthesizer[this._networkType].replace(":address", this._account).replace(":hash", hash))).json();
					if (res.tokenId || this._tryCountForSynthesizer > 10) {
						this.nftForged = res.attribute[0].value;
						return callback(res);
					} else {
						this._tryCountForSynthesizer++;
						return this.detectSynthesizerAPI(delay, hash, callback);
					}
				} catch (error) {
					return console.error(error);
				}
			}, { timeout: delay });
		}, delay);
	},

	getForgingLogs: function (callback) {
		window.requestIdleCallback(async () => {
			const howManyLogs = 15;
			try {
				const res = await (await fetch(config.torchlight.forgingLogsAPI[this._networkType])).json();

				let tempLogs;
				if (res.length < howManyLogs) {
					return callback(res);
				} else {
					tempLogs = res.filter(item => item.level === 3);
				}

				if (tempLogs.length < howManyLogs) {
					tempLogs = tempLogs.concat(res.filter(item => item.level === 2).slice(0, howManyLogs - tempLogs.length));
				}

				if (tempLogs.length < howManyLogs) {
					tempLogs = tempLogs.concat(res.filter(item => item.level === 1).slice(0, howManyLogs - tempLogs.length));
				}

				if (res && callback) {
					return callback(tempLogs);
				}
			} catch (error) {
				console.warn(error);
			}
		});
	},

	_getAirdrop: async function () {
		let res = null;

		try {
			res = await (await fetch(config.torchlight.airdropAPI, {
				method: 'POST',
				mode: 'cors',
				headers: { 'Content-Type': 'application/json' },
				body: JSON.stringify({ account: this._account })
			})).json();
		} catch (error) {
			return console.error(error);
		}

		if (res?.code === 0) {
			res.data.map(item => this._steps[parseInt(item.round) - 1].reward = new BigNumber(item.mintBalance));
		}
	},

	_getNFTContract: async function () {
		if (!this._NFTContract) {
			const abi = await globalUtils.loadABI(config.torchlight.ABI);
			this._NFTContract = new this._web3.eth.Contract(abi, config.torchlight.address[this._networkType])
		}

		return this._NFTContract;
	},

	_runContractMethod: function (func, args, callback, revertCallback, options, hashCallback) {
		return func(...args).send({
			from: this._account,
			...options
		}).on('transactionHash', function (hash) {
			if (hashCallback) {
				return hashCallback(hash);
			}
		}).on('confirmation', function (confirmationNumber, receipt) {
			// 
		}).on('receipt', function (receipt) {
			if (callback) {
				return callback(receipt);
			}
		}).on('error', function (error, receipt) {
			if (revertCallback) {
				return revertCallback(error, receipt);
			}
		});
	},
};