import React, { useEffect } from "react";
import styles from "./InfiniteList.module.scss";

let infiniteListTimer = null;
let infiniteListStartTop = 0;
let infiniteListContainer = null;
let infiniteListContent = null;
let InfiniteListItemHeight = 0;

export const InfiniteList = props => {
	const scorll = () => {
		infiniteListContent.className = styles.content;
		infiniteListStartTop -= InfiniteListItemHeight;
		infiniteListContent.style.top = infiniteListStartTop + "px";

		if (infiniteListStartTop < -(infiniteListContent.offsetHeight - infiniteListContainer.offsetHeight)) {
			const r = infiniteListContent.childNodes[0].cloneNode(true);
			infiniteListContent.appendChild(r)
		}

		if (infiniteListStartTop < -infiniteListContent.childNodes[0].offsetHeight) {
			setTimeout(() => {
				infiniteListContent.className = styles.contentWithoutTransation;
				infiniteListContent.removeChild(infiniteListContent.childNodes[0]);
				infiniteListStartTop = -InfiniteListItemHeight;
				infiniteListContent.style.top = infiniteListStartTop + "px";
			}, 1000);
		}
	};

	const destroyTimer = () => {
		window.clearInterval(infiniteListTimer);
		infiniteListTimer = null;
	};

	useEffect(() => {
		if (!props.scroll) {
			return;
		}

		if (infiniteListTimer) {
			destroyTimer();
		}

		infiniteListContainer = document.getElementById("infiniteListContainer");
		infiniteListContent = document.getElementById("infiniteListContent");
		InfiniteListItemHeight = infiniteListContent.offsetHeight / props.length;

		infiniteListTimer = setInterval(scorll, props.interval ?? 2000);

		return () => {
			destroyTimer();
		}
	}, []);

	return <div
		id="infiniteListContainer"
		className={styles.infiniteListLayout}>
		<div
			id="infiniteListContent"
			className={styles.content}>{props.children}</div>
	</div>
};