export const recordsData = () => {
  const keys = Object.keys(StepStatus);
  steps.map((step, index) => {
    step.round = index + 1;
    step.time = step.startTime.getMonth() + 1 + "." + step.startTime.getDate() + " - " + (parseInt(step.stopTime.getMonth()) + 1) + "." + step.stopTime.getDate();
    step.boostTime = step.startBoost.getMonth() + 1 + "." + step.startBoost.getDate() + " - " + (parseInt(step.stopBoost.getMonth()) + 1) + "." + step.stopBoost.getDate();
    step.reward = step.reward?.gt(0) ? step.reward : globalUtils.zero;
    step.actionStatus = keys[step.status];
  });
  return steps;
}