import React from "react";
import styles from "./BorderedContainer.module.scss";

const BorderTop = props => (<svg width="100%" viewBox="0 0 1199 60" fill="none" xmlns="http://www.w3.org/2000/svg">
	<path d="M1 60L1 1L1156.49 1L1198 39.7768L1198 61" fill="none" stroke="#FF8E3D" strokeWidth={props.strokeWidth ? props.strokeWidth + "px" : "1px"} />
</svg>);

const BorderBottom = props => {
	return <svg width="100%" viewBox="0 0 1199 40" fill="none" xmlns="http://www.w3.org/2000/svg">
		<path d="M0 0L38 38L1198.5 38L1198.5 0" fill="none" stroke="#FF8E3D" strokeWidth={props.strokeWidth ? props.strokeWidth + "px" : "1px"} />
	</svg>
};

export const BorderedContainer = props => {
	return <div className={styles.borderedContainer}>
		<div className={styles.titleBar}>
			<div className={styles.forgeBG}>
				<BorderTop strokeWidth={props.strokeWidth} />
			</div>

			{props.titleBarContent}
		</div>

		<div className={styles.contentContainer}>
			{props.children}
		</div>

		<div className={styles.footBar}>
			<div className={styles.forgeBG}>
				<BorderBottom strokeWidth={props.strokeWidth} />
			</div>
		</div>
	</div>
};